body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.jumbotron-fluid {
    background-color: none !important;
    height: 100px; 
    width: 100%;
    margin-top: 10%; 
    text-align: center
}

@media screen and (max-width: 768px) {
    .jumbotron-fluid {
        margin-top: 20%;
    }
}
.card{
    border-radius: 10px;
    background-color: lightgrey
}

.card h3 {
    color: white;
}

.saved-list{
    border-radius: 15px;
    background-color: lightyellow
}

.deleteBook {
    border-radius: 5px;
    margin-left: 10px
}

.viewBook {
    border-radius: 5px;
    margin-left: 5px
}

.bookDescription, .bookTitle {
    color: black;
}

@media screen and (max-width: 768px) {
    .saved-list {
        margin: 0;
        margin-bottom: 5%;
    }
}
form{
    background: lightblue;
    border-radius: 15px;
    margin-top: 10%;
}
.BookSearchLabel{
    margin-left:20px;
    margin-top: 10px
}
.submitBtn{
    margin-bottom: 10px;
    margin-left: 20px;
}
.form-control{
    margin-left: 20px;
    width: 95%
}

@media screen and (max-width: 768px) {
    
    form {
        margin-top: 30%;
    }
    .form-control {
        width: 85%;
    }
}
.card{
    border-radius: 10px;
    background-color: rgb(71, 71, 71);
    color: white;
}

.search-list{
    border-radius: 15px;
    background-color: lightblue;
    padding: 30px;
    margin: 30px;
}

.saveBook {
    border-radius: 5px;
    margin-left: 10px
}

.viewBook {
    border-radius: 5px;
    margin-left: 5px
}

.bookImage {
    width: 200px;
}

@media screen and (max-width: 768px) {
    .search-list {
        margin: 0;
        margin-bottom: 5%;
    }
}
body{
    background: linear-gradient(-225deg, rgba(2, 84, 131, 0.8), rgba(6, 29, 235, 0.8)),
    url(/static/media/bookimage.c4a1edbe.jpg) no-repeat center center / cover;
    background-size: cover;
    height: 100vh;
}
